import React from 'react';
import classes from '../styles/StartGameModal.module.css';
import Modal from './assets/Modal';
import {BtnConfig} from "./assets/buttons";
import {corpus} from './classes/enums';

const GameText = (props) => {

    return (
        <Modal>
            <h2>Game:   </h2>
            <p className={classes.Instructions}>Please choose a text</p>
            <div className={'container'}>
                <div className={'row'}>
                    <BtnConfig setConfig={() => props.setText(corpus.TANAKH)}>Tanakh</BtnConfig>
                    <BtnConfig setConfig={() => props.setText(corpus.MISHNAH)}>Mishnah</BtnConfig>
                    <BtnConfig setConfig={() => props.setText(corpus.TALMUD)}>Talmud</BtnConfig>
                </div>
            </div>
        </Modal>
    )
}

export default GameText;