import React, {Component} from 'react';
import MatchingRound from './MatchingRound';
import EndGameModal from './EndGameModal';
import GameLang from './GameLang';
import GameText from './GameText';
import GameLevel from './GameLevel';
import StartGameModal from './StartGameModal';
import {levels,lang,corpus} from './classes/enums';




export default class MatchingGame extends Component {
    constructor() {
        super();
        this.state = {
            lang: lang.ENGLISH,
            corpus: corpus.TANAKH,
            score: 0,
            round: 1,
            gamePlay: false,
            gameOver: false,
            gameStart: false,
            answerInform: false,
            gameLang: true,
            gameCorpus: false,
            rounds: 10,
            level: levels.MEDIUM
        }
        this.updateScoreHandler = this.updateScoreHandler.bind(this);
        this.setAnswerInform = this.setAnswerInform.bind(this);
        this.startGame = this.startGame.bind(this);
        this.setLang = this.setLang.bind(this);
    }
    componentDidMount() {

    }

    updateScoreHandler = (score) => {
        if(this.state.round == this.state.rounds){
            this.setState( {
                score: this.state.score + score,
                gameOver: true,
                gamePlay: false,
                answerInform:true
            })
        }
        else{
            this.setState( {
                score: this.state.score + score,
                round: this.state.round + 1,
                answerInform:true
            })
        }
    }

    startGame = () => {
        this.setState({
            gamePlay: true,
            score: 0,
            round: 1,
            gameOver: false,
        })
    }

    setAnswerInform = (status) => {
        //this is dump fix it
        this.setState({
            answerInform: status
        })
    }

    setLang = (lang) => {
        this.setState({
            lang: lang,
            gameLang: false,
            gameCorpus: true,

        })
    }
    //reset texts, will need to reset clock
    setText = (corpus) => {
        this.setState({
            corpus: corpus,
            gameCorpus: false,
            gameLevel: true
        })
    }

    setLevel = (level) => {
        this.setState({
            level: level,
            gameLevel: false,
            gameStart: true,
        })
    }

    render() {
        let currentDisplay;
        if(this.state.gameOver && !(this.state.answerInform)){
            currentDisplay = <EndGameModal reStartGame={this.startGame} score={this.state.score}/>;

        }
        else if(this.state.gamePlay || this.state.answerInform){
            currentDisplay = <MatchingRound updateScoreHandler={this.updateScoreHandler}
                                            setAnswerInform={this.setAnswerInform}
                                            round={this.state.round}
                                            score={this.state.score}
                                            lang={this.state.lang}
                                            corpus={this.state.corpus}
                                            level={this.state.level}
                                            answerInform={this.state.answerInform}/>;

        }
        else if(this.state.gameStart){
            currentDisplay = <StartGameModal startGame={this.startGame}></StartGameModal>;
        }
        else if(this.state.gameLang){
            currentDisplay = <GameLang setLang={this.setLang}/>;
        }
        else if(this.state.gameCorpus){
            currentDisplay = <GameText setText={this.setText}/>;
        }
        else if(this.state.gameLevel){
            currentDisplay = <GameLevel setLevel={this.setLevel}/>;
        }
        return (
            <div>
                {currentDisplay}
            </div>
        )
    }
}