import React, {Component} from 'react';
import classes from '../styles/AnswerInformer.module.css';
import {corpus, lang, levels} from './classes/enums';
import {BtnSmall} from "./assets/buttons";

export default class AnswerInformer extends Component{
    constructor(props) {
        super(props);
        this.state = {
            clueShown: 0
        }
        this.nextClueContext = this.nextClueContext.bind(this)
    }

    nextClueContext = () => {
        this.setState({
            clueShown: this.state.clueShown + 1
        })
    }
    render() {
        let colorClass = this.props.correct ? classes.Correct : classes.Incorrect;
        let langClass = this.props.lang == lang.HEBREW ? classes.Hebrew : classes.English;
        let answer = <p>Correct Answer: {this.props.clues[0].textNameEnglish}</p>;
        let correct = <p className={colorClass}> {this.props.correct ? 'Correct!' : 'Incorrect'}</p>;
        let points = <p>+{this.props.addedPoints}  </p>;

        let contextBoxes = this.props.clues.map((answer, index) => {
            let beforeContext = '';
            let contextRange = {
                hebrew: {tanakh: 3, mishnah: 1, talmud: 2},
                english: {tanakh: 3, mishnah: 1, talmud: 1}
            }[this.props.lang][this.props.corpus]
            for (let i = this.props.clues[index].verseNumber - contextRange; i < this.props.clues[index].verseNumber; i++) {
                if (i >= 0) {
                    if (this.props.lang == lang.HEBREW) {
                        beforeContext += this.props.clues[index].textHebrew[i] + ' ';
                    } else {
                        beforeContext += this.props.clues[index].textEnglish[i] + ' ';
                    }
                }
            }
            let answerText = <span
                className={colorClass}>{this.props.lang == lang.HEBREW ? this.props.clues[index].textHebrew[this.props.clues[index].verseNumber] + " " :
                this.props.clues[index].textEnglish[this.props.clues[index].verseNumber] + " "}</span>

            let afterContext = '';
            for (let i = this.props.clues[index].verseNumber + 1; i <= this.props.clues[index].verseNumber + contextRange && i < this.props.clues[index].textHebrew.length; i++) {
                if (i >= 0) {
                    if (this.props.lang == lang.HEBREW) {
                        afterContext += this.props.clues[index].textHebrew[i] + ' ';
                    } else {
                        afterContext += this.props.clues[index].textEnglish[i] + ' ';
                    }
                }
            }
            let answerContext = <p>{beforeContext}{answerText}{afterContext}</p>;
            return (<ContextBox
                textNameHebrew={this.props.clues[index].textNameHebrew}
                chapter={this.props.clues[index].chapter}
                verseNumber={this.props.clues[index].verseNumber}
                langClass={langClass}
                answerContext={answerContext}
                onClick={this.nextClueContext}
            />)

        })


        return (
            <div className={classes.AnswerInformer}>
                <div className={classes.Header}>
                    {correct}
                    {this.props.correct ? points : answer}
                    {contextBoxes[this.state.clueShown]}
                </div>

            </div>
        )
    }

}


const ContextBox = (props) => {
    return(
        <div className={classes.Context}>
            <p className={classes.Title }>{props.textNameHebrew + " " + props.chapter + ":" + (props.verseNumber + 1)} </p>
            <p className={props.langClass}>{props.answerContext}</p>
            <BtnSmall onClick={props.onClick}>Next Context</BtnSmall>
        </div>
    )
}