import React, {Component} from 'react';
import Crossword from "./Crossword";
import {xWords} from "./classes/xword";

// const MATRIX = [{x: 0, y: 0, d: 2, clue: "", num: 1},{x: 4, y: 0, d: 2, clue: "", num: 2},{x: 0, y: 2, d: 1, clue: "", num: 3},
// {x: 3, y: 2, d: 2, num: 4, clue: ""}, {x: 5, y: 2, d: 2, clue: "", num: 5},{x: 10, y: 3, d: 2, clue: "", num: 6},{x: 8, y: 4, d: 2, clue: "", num: 7},
//     {x: 5, y: 5, d: 1, clue: "", num: 8},{x: 2, y: 8, d: 1, clue: "", num: 9}]
// const MATRIX =[{x:0,y:0,d:2,clue:"",num: 1},
//     {x:6,y:0,d:2,clue:"",num:2},{x:0,y:3,d:1,clue:"",num:1}]
const CLUE_LIST = [{word: "", clue: "sad month"},{word: "", clue: "happy month"},{word: "", clue: "And God Said to Moses"},{word: "", clue: "The israeli basketball club"},{word: "", clue: "Who is with god? Come with me"},
    {word: "", clue: "fast month"},{word: "", clue: "f month"},{word: "", clue: "the boss"},{word: "", clue: "it just is"} ]

const WORD_LIST = ["","","", "", "","", "", "", ""]


export default class CrosswordGenerator extends Component {
    constructor(props) {
        super(props);
        xWords.Create(11,11,WORD_LIST);
        this.state = {
            dimensions: [11, 11],
            gridMatrix: xWords.GetQuestionList(),//an array of objects x,y,dir,string,number
            clueList: CLUE_LIST,
            grid: [],
            clues: {}
        }
        ;
    }
    componentDidMount() {
        this.structure_Grid_Data();
    }

    structure_Grid_Data = () =>{
        let grid = [];
        let clues = {};
        for(let i = 1; i <= this.state.dimensions[0]; i++){
            for(let j = 0; j < this.state.dimensions[1]; j++){
                let id = String.fromCharCode(65 + j) + i;
                console.log("id : " + id);
                grid.push(new Grid_Data(id,null,null,null));
            }
        }
        console.log(grid);

        let labelCount = 1;
        let currentLabel = 0;
        let labelOffset = 0;

        this.state.gridMatrix.forEach( word => {
            let boxes = [];
            [...word.clue].forEach( (letter, i) => {
                let xComponent;
                let yComponent;
                if(word.d == 1){
                    xComponent =String.fromCharCode(65 + word.x + i);
                    yComponent = word.y + 1;
                }
                else{
                    xComponent =String.fromCharCode(65 + word.x);
                    yComponent = word.y + 1 + i;
                }
                let id = xComponent + yComponent;
                boxes.push(id);
                console.log("id2 : " + id);
                let data_to_change = grid.find(x => x.id === id);
                data_to_change.letter = word.clue.charAt(i);
                let dirComponent = (word.d == 1) ? "Ac":"Dn";
                if(data_to_change.clues != null){
                    data_to_change.clues.push(dirComponent + word.num);
                }
                else{
                    data_to_change.clues = [dirComponent + word.num];
                }
                if(word.d == 1){
                    if(i == word.clue.length - 1){
                        if(data_to_change.label != null){
                            currentLabel = data_to_change.label;
                            labelOffset++;
                        }
                        else{
                            data_to_change.label = labelCount - labelOffset;
                            currentLabel = labelCount - labelOffset;
                        }
                    }

                }
                else{
                    if(i == 0){
                        if(data_to_change.label != null){
                            currentLabel = data_to_change.label;
                            labelOffset++;
                        }
                        else{
                            data_to_change.label = labelCount - labelOffset;
                            currentLabel = labelCount - labelOffset;
                        }
                    }
                }
            })
            let clue = this.state.clueList.find(x => x.word == word.clue).clue;
            let direction = (word.d == 1) ? "across" : "down";
            let dirKey = ((word.d == 1) ? "Ac":"Dn") + word.num;
            clues[dirKey] =  new Clue_Data(clue,word.clue,direction,currentLabel, boxes);
            labelCount++;
        });
        this.setState({
            grid: grid,
            clues: clues
        })
    }




    render() {
        return (
            <div className={'col-sm '}>
                <Crossword clues={this.state.clues} grid={this.state.grid} dimensions={this.state.dimensions}/>
            </div>

        )
    }
}



class Grid_Data{
    constructor(id,letter,clues,label){
        this.id = id;
        this.letter = letter;
        this.clues = clues;
        this.label = label
    }
}

class Clue_Data{
        constructor(clue,answer,direction,number,boxes){
            this.clue = clue;
            this.answer = answer;
            this.direction = direction;
            this.number = number;
            this.boxes = boxes;
        }
}