import React, {Component} from 'react';


export default class TimeBox extends Component {
    constructor(props) {
        super(props);
        this.state = {
            count: this.props.time,
            timerInterval: '',

        };

    }

    componentDidMount() {
        this.startCountDown()
    }

    componentDidUpdate(prevProps, prevState, snapshot) {
        if (prevProps != this.props) {
            if (this.props.round != prevProps.round) {
                this.setState({
                    count: this.props.time
                })
                clearInterval(this.state.timeInterval);
                this.startCountDown();
            }
        }
    }


    startCountDown(){

        let interval = setInterval(function () {
                    this.setState({count: this.state.count - 1}, ()=> {
                        if(this.state.count <= 0){
                            this.props.resetClue();
                            clearInterval(this.state.timeInterval);
                            this.startCountDown();
                        }
                        else if(this.state.count % 15 == 0){
                            this.props.resetClue();
                        }
                    });

        }.bind(this), 1000);

        this.setState({
            timeInterval: interval
        })
    }

    // timer(){
    //     this.setState({count: this.state.count + 1});
    // }




    render() {
        return (
            <div className='col-12'>
                <h2 style={{display: 'inline-block'}}>Round: {this.props.round}  </h2>
                <h2 style={{display: 'inline-block', marginLeft:'20px'}}>Time for Clue: {this.state.count}</h2>
            </div>

        )
    }
}