import React, {Component} from 'react';
import TextBox from './TextBox';
import TimeBox from './assets/TimeBox';
import ScoreBoard from './ScoreBoard'
import AnswerInformer from './AnswerInformer';
import {BtnAnswer, BtnSmall} from "./assets/buttons";
import classes from '../styles/MatchingRound.module.css'
import Utils from "./classes/Utils";
import {TextChapters} from "./classes/texts";
import {levels,lang,corpus} from './classes/enums'

const tanakhDE = require('./dicts/tanakh_easy.json')
const mishnahDE = require('./dicts/mishnah_easy.json')
const talmudDE = require('./dicts/talmud_easy.json')
const tanakhDM = require('./dicts/tanakh_medium.json')
const mishnahDM = require('./dicts/mishnah_medium.json')
const talmudDM = require('./dicts/talmud_medium_stop_words.json')
const tanakhBG = require('./dicts/tanakh_book_groups.json')
const tanakhCG = require('./dicts/tanakh_corpus_groups.json')
const mishnahBG = require('./dicts/mishnah_book_groups.json')
const mishnahCG = require('./dicts/mishnah_corpus_groups.json')
const talmudBG = require('./dicts/talmud_book_groups.json')
const talmudCG = require('./dicts/talmud_corpus_groups.json')


const dictFinder = {
    easy: {
        tanakh: tanakhDE,
        mishnah: mishnahDE,
        talmud: talmudDE
    },
    medium: {
        tanakh: tanakhDM,
        mishnah: mishnahDM,
        talmud: talmudDM
    },
    corpusGroups: {
        tanakh: tanakhCG,
        mishnah: mishnahCG,
        talmud: talmudCG
    },
    bookGroups: {
        tanakh: tanakhBG,
        mishnah: mishnahBG,
        talmud: talmudBG
    }
}

const U = new Utils();


export default class MatchingRound extends Component {
    constructor(props) {
        super(props);
        let labelTextNames = this.selectRandomTexts(3, TextChapters[this.props.corpus],this.props.level,dictFinder['corpusGroups'][this.props.corpus],dictFinder['bookGroups'][this.props.corpus]);
        this.state = {
            labelTextNames: labelTextNames,
            textInfos: [],
            labels: [],
            clue: 1,
            answeredCorrectly: false,
            correctAnswer: '',
            addedPoints: 0,
        };
    }


    componentDidMount(){
        this.getTextInfo(this.state.labelTextNames);
        this.getLabels(this.state.labelTextNames);
    }


    getTextInfo(textNames) {
        let singleTextInfoArray = [];
        let correctAnswer = textNames[0];
        for (let i = 0; i < textNames.length; i++) {
            singleTextInfoArray.push(correctAnswer);
        }
        Promise.all(singleTextInfoArray.map((textName, index) => {
            let chapterNumber = this.chooseRandomChapter(textName,TextChapters[this.props.corpus]);
            let fetchString = 'http://www.sefaria.org/api/texts/' + textName + '.' + chapterNumber;
            return fetch(fetchString)
                .then((response) => {
                    return response.json();
                }).then((data) => {
                    data['text'] = U.removeHTML(data.text);
                    let [vn,names] = this.chooseRandomVerse(data.he,textName,index);
                    return(new TextInfo(data.indexTitle, data.heTitle,vn,chapterNumber, data.he, data.text, names));
                });
        })).then(values => {
            console.log("Text Values");
            console.log(values);
            this.setState({
                textInfos : values,
                count: this.state.count + 3
            }, ()=> console.log("finished changes texts"));
        })
    }


    getLabels(textInfoArray){
        Promise.all(textInfoArray.map(textName => {
            //this doesn't really need to have a network call
            let fetchString = 'http://www.sefaria.org/api/texts/' + textName;
            return fetch(fetchString)
                .then((response) => {
                    return response.json();
                }).then((data) => {
                    data['text'] = U.removeHTML(data.text);
                    // console.log(data);
                    let correct = (textName == textInfoArray[0]);
                    return (new Label(data.indexTitle.replace(/Mishnah/, ''),data.heIndexTitle.replace('' ,''), correct));
                });
        })).then(values => {
            console.log("Label values");
            console.log(values);
            U.shuffleArray(values)
            this.setState({
                labels : values
            }, ()=> console.log("finished changes labels"));
        });
    }

    selectRandomTexts(num, textArr, level,corpusGroups,bookGroups){
        let texts = Object.keys(textArr);
        let textOptions = [...texts]
        let newUserTexts = [];

        let index = Math.floor(Math.random() * textOptions.length);
        let correctText = textOptions[index];
        newUserTexts.push(textOptions[index]);
        textOptions.splice(index,1);
        if(level == levels.HARD){
            let textGroupName = bookGroups[newUserTexts[0]][Math.floor(Math.random() * bookGroups[newUserTexts[0]].length)];
            textOptions = corpusGroups[textGroupName];
        }

        for (let i = 1; i < num; i++) {
            do {
                index = Math.floor(Math.random() * textOptions.length);
            }while(textOptions[index] == correctText)
            newUserTexts.push(textOptions[index]);
            textOptions.splice(index, 1);
        }
        console.log("Pre-Selected " + newUserTexts);
        return newUserTexts;
    }

    resetRoundHandler = (score,correct) =>{
        this.labelTextNames = this.selectRandomTexts(3, TextChapters[this.props.corpus],this.props.level,dictFinder['corpusGroups'][this.props.corpus], dictFinder['bookGroups'][this.props.corpus] );
        console.log("After Click " + this.labelTextNames);
        this.props.updateScoreHandler(score);
        let correctAnswer = this.state.textInfos;
        this.setState({
            labelTextNames : this.labelTextNames,
            clue: 1,
            answeredCorrectly: correct,
            correctAnswer: correctAnswer,
            addedPoints: score
        }, () => {
            console.log("Post Click state" + this.state.labelTextNames);
            this.correctAnswerShow();
            this.getTextInfo(this.state.labelTextNames);
            this.getLabels(this.state.labelTextNames);
        });
    }

    correctAnswerShow = () =>{

        let timer = setInterval( () => {
            clearInterval(timer);
            this.props.setAnswerInform(false);
        },3000)

    }

    resetClue = () =>{
        if(this.state.clue == 3){
            this.resetRoundHandler(0, false);
        }
        else{
            this.setState({
                clue : this.state.clue + 1
            });
        }
    }


    chooseRandomChapter(textName, textDict){
        //lookup chapter numbers for a given book
         let num = Math.ceil(Math.random() * textDict[textName]);

         //special logic for talmud, includes amudim
         if(Object.keys(textDict)[0] == 'Berakhot' && textDict[Object.keys(textDict)[0]] == 64){
             //tamid starts at daf 26 and end at 33
             if(textName == "Tamid"){
                 num = (num % 6) +26;
                 let daf = Math.round(Math.random())
                 return num.toString() + (daf == 0 ? 'a':'b');
             }
             //accounting for th lack of daf aleph
             if(num == 1){
                 num = 2;
                 let daf = Math.round(Math.random())
                 return num.toString() + (daf == 0 ? 'a':'b');
             }
             num = num - 1;
             let daf = Math.round(Math.random());
             return num.toString() + (daf == 0 ? 'a':'b');
         }
        return num;
    }

    chooseRandomVerse(textArr,textName,index){
        //identify clue difficulty, clue number and difficulty
        //need to also not reuse verse -- keep track of in game
        let [level,names] = this.getClueDifficulty(index);
        let candidateVerse = Math.floor(Math.random() * textArr.length);
        let valid = false
        let candidateText =  textArr[candidateVerse];
        //check to see if this verse is valid, if not look through other verses in chapter
        for(let i = candidateVerse; i < textArr.length && valid == false; i++){
            candidateText = textArr[i % textArr.length];
            let candidateArr = U.stripVowels(candidateText).split(' ');
            //see if verse is appropriately hard based on give-away lists
            if(level == levels.HARD){
                let giveAwayList = dictFinder[levels.EASY][this.props.corpus][textName];
                if(!candidateArr.some(w=> giveAwayList.indexOf(w) >= 0)){
                    valid = true;
                    console.log("Found a match!")
                    candidateVerse = i % textArr.length;
                }
            }
            else{
                //ensure that a word from the easy list or medium list is in the verse
                let wordList = [];
                if(level == levels.EASY){
                    wordList = dictFinder[level][this.props.corpus][textName];
                }
                else if(level == levels.MEDIUM){
                    wordList = dictFinder[level][this.props.corpus][textName]['unigrams'];
                }

                console.log(wordList)
                console.log(U.stripVowels(candidateText))
                if(candidateArr.some(w=> wordList.indexOf(w) >= 0)){
                    valid = true;
                    console.log("Found a match!")
                    candidateVerse = i % textArr.length;
                }
            }

        }
        return [candidateVerse,names];
    }

    getClueDifficulty(index){
        switch(this.props.level) {
            case levels.EASY:
                return [levels.EASY,true];
                break;
            case levels.MEDIUM:
            case levels.HARD:
                if(index == 0){
                    return [levels.HARD,false];
                }
                else if(index == 1){
                    return [levels.MEDIUM,false];
                }
                else{
                    return [levels.EASY,false];
                }
                break;
        }
    }


render(){
        //Generate textboxes and labels
        let textCompArray = [];
        let labelCompArray = [];
        let nextClue = '';
        if(this.state.textInfos.length > 0) {
            textCompArray = this.state.textInfos.filter((text,i) => {
                if (i <= (this.state.clue - 1)) {
                    return true;
                }
                return false;
            }).map((text, i) => {
            return (<TextBox key={i + (this.state.textInfos.length * this.props.round)}
                             lang={this.props.lang} showText={text}/>)});

            labelCompArray = this.state.labels.map((label, i) => {
                return(<BtnAnswer key={i + (this.state.textInfos.length * this.props.round)}
                                  label={label}
                                  clue={this.state.clue}
                                  lang={this.props.lang}
                                  corpus={this.props.corpus}
                                  resetRoundHandler={this.resetRoundHandler}/>)});
            if(this.state.clue >= 1 && this.state.clue < 3){
                nextClue =<BtnSmall onClick={this.resetClue}>Next Clue</BtnSmall>
            }
        }
        let answerInform = '';
        if(this.props.answerInform){
            answerInform = <AnswerInformer correct={this.state.answeredCorrectly} clues={this.state.correctAnswer} addedPoints={this.state.addedPoints} lang={this.props.lang} corpus={this.props.corpus}/>
        }

    return (
        <div className={'container ' + classes.GameDesk}>
            <h2>Match the Verse to the Correct Text</h2>
                <div className={classes.TextRow}>
                    <div className={'row '}>
                        {this.props.answerInform ? answerInform : textCompArray}
                        {!this.props.answerInform && nextClue}
                </div>
                </div>

            <div className='row'>
                {this.props.answerInform ? <p></p> : labelCompArray}
            </div>
            <ScoreBoard score={this.props.score}/>
            {this.props.answerInform ?
                <p></p> : <TimeBox round={this.props.round}
                                   style={{display: 'inline'}}
                                   resetRoundHandler={this.resetRoundHandler}
                                   resetClue={this.resetClue}
                                   time={45}
                                   interval={15}/>}

        </div>
    )};
}

class TextInfo {
    constructor(textNameEnglish,textNameHebrew,verseNumber,chapter,textHebrew,textEnglish,names){
        this.textNameEnglish = textNameEnglish;
        this.textNameHebrew = textNameHebrew;
        this.verseNumber = verseNumber;
        this.chapter = chapter;
        this.textHebrew = textHebrew;
        this.textEnglish = textEnglish;
        this.names = names;
    }
}

class Label{
    constructor(textNameEnglish, textNameHebrew, correct){
        this.textNameEnglish = textNameEnglish;
        this.textNameHebrew = textNameHebrew;
        this.correct = correct;
    }
}

