import React from 'react';
import classes from '../styles/StartGameModal.module.css';
import Modal from './assets/Modal';
import {BtnConfig} from './assets/buttons';
import {levels} from './classes/enums';


const GameLevel = (props) => {

    return (
        <Modal>
            <h1>Game:   </h1>
            <p className={classes.Instructions}>Please choose a difficulty level</p>
            <div className={'container'}>
                <div className={'row'}>
                    <BtnConfig setConfig={() => props.setLevel(levels.EASY)}>Easy</BtnConfig>
                    <BtnConfig setConfig={() => props.setLevel(levels.MEDIUM)}>Medium</BtnConfig>
                    <BtnConfig setConfig={() => props.setLevel(levels.HARD)}>Hard</BtnConfig>
                </div>
            </div>
        </Modal>
    )
}

export default GameLevel;